import { urlEncode } from "./util"
import User from "./user"

// const BASE_URL = 'https://www.fastmock.site/mock/0834d4826d5be6de92173193ca42e1b9'
// let BASE_URL = 'http://zwb-baojia.zhaodaka.vip/api/wechat'
let BASE_URL = '/api/webchat'
// if (process.env.NODE_ENV === 'development') {
//     BASE_URL = '/api/webchat'
// }

export function httpGet(url, params) {
    params = addCommonParam(params)
    return handleResponse(fetch(BASE_URL + url + '?' + urlEncode(params), {
        mode: 'cors',
    }))
}
export function httpPost(url, formData) {
    formData = addCommonParam(formData)
    return handleResponse(fetch(BASE_URL + url, {
        method: 'POST',
        mode: 'cors',
        body: formData
    }))
}
function handleResponse(fetchPromise) {
    return fetchPromise.then(res => res.json()).then(data => {
        if (data.code === 200) {
            return data
        } else {
            return Promise.reject(data.msg)
        }
    })
}

function addCommonParam(params) {
    const openid = User.getOpenid()
    if (!!openid) {
        if (params.append) {
            params.append('openid', openid)
        } else {
            params.openid = openid
        }
    }
    return params
}